body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  color:white;
  background-color: black;
  padding: 20px 40px;
  justify-content: space-between;
  font-size: 30px;
  display: flex;
}
.cart-icon .counter
{
  height: 18px;
  width:18px;
  background-color: blue;
  border-radius: 50%;
  position: relative;
  right: 10px;
  display: flex;
  align-items: center;
  font-size: small;
  justify-content: center;
}
.cart-icon{
  color: white;
  padding: 10px;
  display: flex;
}
.icon{
  position: relative;
  right: 2px;
  font-size: 20px;

}
img{
  height:150px
}
.card-wrapper
{
  display:flex;
  margin: 40px;
  box-shadow: 2px 2px 7px black;
  width:320px;
}
.image-wrapper{
  width:130px;
  position: relative;
  display:flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid grey;

}
.content
{
  flex-grow:1;
  display: flex;
  flex-direction: column;
  padding: 10px;
  gap:10px
}
.price
{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color:white;
  border-radius: 3px;
}
.quantity
{

  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid grey;
}
.quantity div{
  height:22px;
   width:22px;
   display: flex;
   align-items: center;
   justify-content: center;
}
.quantity >div:first-child,
.quantity >button{
  background-color: rgb(48, 48, 238);
  border-radius: 0;
  color:white;
}
.quantity >button:disabled{
  opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 70vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}

.total
{
  box-sizing: border-box;
  background-color: gray;
  padding: 20px;
  position:fixed;
  bottom:0;
  width: 100%;
  text-align: right;
}